import Form from 'react-bootstrap/Form';

export function Select({ handleSelect }) {
  return (
    <Form.Select
      aria-label="Default select example"
      onChange={e => {
        handleSelect(e.target.value);
      }}
    >
      <option> </option>
      <option value="3"></option>
      <option value="5">'</option>
      <option value="10"></option>
    </Form.Select>
  );
}
